@charset "UTF-8";
.hero {
  background-color: #000;
  height: 100vh;
}
.hero__content {
  display: flex;
  height: 100%;
  font-size: 24px;
}
.hero__bg {
  width: 100px;
  height: 100%;
  position: absolute;
  bottom: 0;
  left: -33px;
  overflow: hidden;
  background-image: url("../assets/images/bg-main.png");
  background-size: contain;
  transform: scale(-1, -1);
}
.hero__bg--mobile {
  display: none;
}
.hero__social {
  display: flex;
  width: 100%;
  gap: 30px;
  justify-content: center;
  margin-top: 1em;
  margin-bottom: 1em;
}
.hero__social svg {
  width: 30px;
  height: 30px;
}
.hero__social-link {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  padding: 4px;
  color: #fff;
}
.hero__social-link::before {
  content: "";
  display: block;
  position: absolute;
  top: -5px;
  left: -5px;
  width: 120%;
  height: 120%;
  background-color: transparent;
  mask-image: url("../assets/images/mask.png");
  mask-size: contain;
  mask-repeat: no-repeat;
  mask-position: center;
  transform: rotate(90deg);
  -webkit-mask-image: url("../assets/images/mask.png");
  -webkit-mask-size: cover;
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-position: center;
  transition: transform 0.3s, background-color 0.3s;
}
.hero__social-link svg {
  position: relative;
  z-index: 1;
  fill: currentColor;
}
.hero__info {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 50%;
  padding: 2em 1em;
  text-align: center;
  overflow: hidden;
}
.hero__avatar {
  margin: 0 auto;
  width: 40%;
  aspect-ratio: 1;
  flex-shrink: 0;
  border-radius: 50%;
  overflow: hidden;
  position: relative;
}
.hero__avatar img {
  position: absolute;
  inset: 0;
  height: 110%;
  width: 110%;
  object-fit: cover;
  object-position: center center;
}
.hero__preview {
  width: 50%;
  aspect-ratio: 1;
  min-height: 60vh;
  overflow: hidden;
  position: relative;
  /* Когда контейнер hero__preview получает класс .is-ready */
}
.hero__preview canvas, .hero__preview img {
  position: absolute;
  inset: 0;
  height: 100%;
  width: 100%;
  object-fit: cover;
  object-position: center center;
  cursor: pointer;
  will-change: transform, contents; /* Добавили contents */
  transform: translateZ(0);
  touch-action: none;
}
.hero__preview.is-ready canvas {
  opacity: 1; /* Показываем канвас */
  pointer-events: auto; /* Канвас начинает реагировать на события */
}
.hero__preview.is-ready img {
  opacity: 0; /* Скрываем изображение */
}

@media (hover: hover) {
  .hero__social-link:hover {
    color: red;
  }
  .hero__social-link:hover::before {
    background-color: #fff;
    transform: none;
  }
}
@media only screen and (max-width: 1199px) {
  .hero__bg {
    left: -62px;
  }
  .hero__content {
    font-size: 18px;
  }
}
@media only screen and (max-width: 1023px) {
  .hero {
    height: auto;
  }
  .hero__bg {
    height: 60px;
    left: 0;
    top: -42px;
    width: 100%;
    background-image: url("../assets/images/bg-mobile.png");
    transform: none;
  }
  .hero__bg--mobile {
    display: flex;
    top: auto;
    bottom: -40px;
    transform: scale(-1, -1);
  }
  .hero__content {
    flex-direction: column;
  }
  .hero__preview {
    width: 100%;
  }
  .hero__info {
    width: 100%;
    justify-content: center;
  }
}